<template>
    <!--Page Title-->
    <section class="page-title">
        <div class="auto-container">
            <div class="title-outer">
                <h1>Find Jobs</h1>
                
            </div>
        <div>
        </div>
        </div>
    </section>
    <!--End Page Title-->

    <!-- Listing Section -->
    <section class="ls-section">
        <div class="auto-container">
        <li
            >
              <router-link to="/dashboard"
                ><i data-feather="home"></i>
                <span>Main Menu Dashboard</span></router-link
              >
            </li>
            <div class="filters-backdrop"></div>

            <div class="row">

                <!-- Filters Column -->
                <div class="filters-column col-lg-4 col-md-12 col-sm-12">
                    <div class="inner-column">
                        <div class="filters-outer">
                            <button type="button" class="theme-btn close-filters">X</button>

                            <h4>Search by Keywords</h4>

                            <el-input v-model="search" @keyup="getJobs()" placeholder="Job title or keywords"
                                style="margin-bottom: 20px" />
                            <h4>Job Industry</h4>
                            <el-select v-model="job_title.industry" @change="getJobs()"
                                placeholder="Select job industry" filterable>
                                <el-option :value="industry.industry"
                                    v-for="industry in $store.state.dictionary.industries" :key="industry.industry"
                                    :label="industry.industry" />
                            </el-select>

                            <h4>Job Type</h4>

                            <el-select v-model="job_title.employment_type" @change="getJobs()">
                                <el-option v-for="employment_type in $store.state.company.employment_types"
                                    :key="employment_type" :label="employment_type.employment_type"
                                    :value="employment_type.employment_type" />
                            </el-select>

                            <h4>Career Level</h4>
                            <el-select v-model="job_title.career_level" @change="getJobs()" filterable
                                placeholder="Select career level">
                                <el-option v-for="level in [
                                    'Entry-level',
                                    'Intermediate',
                                    'Mid-level',
                                    'Senior or executive-level',
                                ]" :key="level" :label="level" :value="level" />
                            </el-select>

                            <h4>Experience</h4>
                            <el-select v-model="job_title.experience" @change="getJobs()"
                                placeholder="Select required years of experience" filterable>
                                <el-option value="0">No Experience/Less than 1 year</el-option>
                                <el-option value="1 year">1 year</el-option>
                                <el-option value="2 years">2 years</el-option>
                                <el-option value="3 years">3 years</el-option>
                                <el-option value="4 years">4 years</el-option>
                                <el-option value="5 years">5 years</el-option>
                                <el-option value="6 years">6 years</el-option>
                                <el-option value="7 years">7 years</el-option>
                                <el-option value="8 years">8 years</el-option>
                                <el-option value="9 years">9 years</el-option>
                                <el-option value="10 years">10 years</el-option>
                                <el-option value="11 years">11 years</el-option>
                                <el-option value="12 years">12 years</el-option>
                                <el-option value="13 years">13 years</el-option>
                                <el-option value="14 years">14 years</el-option>
                                <el-option value="15 years">15 years</el-option>
                                <el-option value="16 years">16 years</el-option>
                                <el-option value="17 years">17 years</el-option>
                                <el-option value="18 years">18 years</el-option>
                                <el-option value="19 years">19 years</el-option>
                                <el-option value="20 years">20 years</el-option>
                                <el-option value="More than 20 years">More than 20 years</el-option>
                            </el-select>

                            <h4>Location</h4>
                            <el-select v-model="job_title.country" @change="getJobs()" filterable
                                placeholder="Select country">
                                <el-option v-for="country in $store.state.dictionary.countries" :key="country"
                                    :label="country.name" :value="country.name" />
                            </el-select>
                            <el-row>
                                <el-col :span="6">
                                    <el-form-item label="">
                                        <el-button type="primary" plain @click="getJobs()"
                                            :loading="loading">Filter</el-button>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="6">
                                    <el-form-item label="">
                                        <el-button type="primary" plain @click="job_title = {}"
                                            :loading="loading">Reset</el-button>
                                    </el-form-item>
                                </el-col></el-row>
                        </div>
                    </div>
                </div>

                <!-- Content Column -->
                <div class="content-column col-lg-8 col-md-12 col-sm-12">
                    <div class="ls-outer">
                        <!-- Job Block -->
                        <div v-if="jobs && jobs.length > 0" class="job-block" v-for="job in jobs" :key="job.id">
                            <div class="inner-box">
                                <div class="content">
                                    <span class="company-logo"><img :src="job.company_logo" alt=""></span>
                                    <h4><router-link to="#" @click="jobDetails(job)">{{ job.job_title }}</router-link>
                                    </h4>
                                    <ul class="job-info">
                                        <li><i class="fa fa-briefcase text-purple"></i> {{ job.industry }}</li>
                                        <li><i class="fa fa-map-marker text-info"></i> {{
                                            job.location + ", " +
                                                job.country
                                        }}</li>
                                        <li><i class="fa fa-clock text-danger"></i> {{
                                            $helpers.dateFormat(job.application_deadline)
                                        }}</li>
                                        <li><i class="fa fa-graduation-cap text-warning"></i> {{ job.experience }}
                                        </li>
                                    </ul>
                                    <ul class="job-other-info">
                                        <router-link to="/login">
                                            <li class="privacy">Apply Now</li>
                                        </router-link>
                                        <li class="time">{{ job.job_type }}</li>
                                        <li class="required"> {{ job.career_level }}</li>
                                    </ul>
                                    <button class="bookmark-btn"><i class="fa fa-bookmark text-info"></i></button>
                                </div>
                            </div>
                        </div>
                        <el-alert v-else
                            title="Unfortunately, at this time, we don't have any job vacancies. Please check later"
                            type="warning" />
                        <el-pagination v-if="jobs && jobs.length > 0" background
                            layout="total, sizes, prev, pager, next" :page-sizes="[10, 25, 50, 100]"
                            :total="pagination.meta ? pagination.meta.total : 0" @size-change="handleSizeChange"
                            @current-change="handleCurrentChange" />
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
<style scoped>
@import './css/bootstrap.css';
@import './css/style.css';
@import './css/responsive.css';

.el-select {
    height: 60px;
}
</style>

<script>
import JobService from "../../services/company.service";
export default {
    data() {
        return {
            dialog: false,
            loading: false,
            pagination: { page: 1, size: 10 },
            search: "",
            jobs: [],
            job_title: {}
        };
    },
    mounted() {
        this.getJobs();
        this.$store.dispatch("dictionary/getCountries");
        this.$store.dispatch("dictionary/getIndustries");
        this.getJobs();
    },
    methods: {
        jobDetails(job) {
            localStorage.setItem("job", JSON.stringify(job));
            this.$router.push("/job-details");
        },
        handleCurrentChange(val) {
            this.pagination.page = val;
            this.getJobs();
        },
        handleSizeChange(val) {
            this.pagination.size = val;
            this.getJobs();
        },
        getJobs() {
            this.loading = true;
            JobService.getJobs({
                page: this.pagination.page,
                size: this.pagination.size,
                search: this.search,
                filter: this.job_title
            }).then(
                (res) => {
                    this.jobs = res.data;
                    this.pagination.meta = res.meta;
                    this.loading = false;
                },
                (error) => {
                    this.loading = false;
                    this.message =
                        (error.response &&
                            error.response.data &&
                            error.response.data.message) ||
                        error.message ||
                        error.toString();
                    this.$notify({
                        title: "Error",
                        message: this.message,
                        type: "error",
                    });
                }
            );
        },
    },
};
</script>